.app__aboutus {
    position: relative;
}

.app__aboutus-overlay {
    position: absolute;
    inset: 0;
}

.app__aboutus-overlay img {
    width: 391px;
    height: 415px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: right;
    border-top: 2px solid var(--color-golden);
    border-left: 2px solid var(--color-golden);
}

.app__aboutus-content_knife {
    margin: 2rem 4rem;
}

.app__aboutus-content_knife img {
    height: 810px;
}

.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    border-bottom: 2px solid var(--color-golden);
    border-right: 2px solid var(--color-golden);
}

.app__aboutus-content_history p,
.app__aboutus-content_about p {
    margin: 2rem 0;
    height: 150px;
    color: var(--color-grey);
}

@media screen and (max-width: 2000px) {
    .app__aboutus-content_knife img {
        height: 900px;
    }

    .app__aboutus-content_history p,
    .app__aboutus-content_about p {
    margin: 2rem 0;
    height: 150px;
    color: var(--color-grey);
    }
}

@media screen and (max-width: 1450px) {
    .app__aboutus-content_about p {
        height: 370px;
    }
    .app__aboutus-content_history p {
        height: 370px;
    }
}

@media screen and (max-width: 900px) {
    .app__aboutus-content {
        flex-direction: column;
    }

    .app__aboutus-content_knife {
        margin: 4rem 0;
    }

    .app__aboutus-content_knife img {
        height: 700px;
    }

    .app__aboutus-content_about {
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .app__aboutus-content_history {
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img {
        width: 80%;
        height: 320px;
    }

    .app__aboutus-content_knife img {
        height: 600px;
    }

    .app__aboutus-content_about {
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .app__aboutus-content_history {
        justify-content: center;
        align-items: center;
        text-align: center;
    }

    .app__aboutus-content_about p {
        height: 155px;
    }
    .app__aboutus-content_history p {
        height: 185px;
    }
}